import React from 'react';
import styled from 'styled-components';
import { colorOptions } from '../theme';

const Wrapper = styled.span`
    display: inline-block;
    position: relative;
    color: ${props => props.textColor};

    &::before {
        content: '';
        position: absolute;
        height: 40%;
        width: 0%;
        background-color: ${props => props.backgroundColor};
        opacity: 0;
        bottom: 0%;
        left: -10%;
        transition: width 0.2s ease-in-out;
        z-index: -1;
    }

    &:hover {
        &::before {
            opacity: 1;
            width: 120%;
        }
    }
`;

const HoverEffectText = ({children, textColor, backgroundColor }) => {
    return (
        <Wrapper textColor={textColor} backgroundColor={backgroundColor}>
            {children}
        </Wrapper>
    )
}

HoverEffectText.defaultProps = {
    textColor: `${colorOptions.red.PRIMARY_COLOR}`,
    backgroundColor: `${colorOptions.red.PRIMARY_COLOR_LIGHT}`
}


export default HoverEffectText;